import React from 'react'
import twoLogo from '../images/twoLogo.png'
import twoItem1 from '../images/twoItem1.png'
import twoItem2 from '../images/twoItem2.png'
import twoItem3 from '../images/twoItem3.png'
import twoItem11 from '../images/twoItem1-1.png'
import twoItem12 from '../images/twoItem1-2.png'
import twoItem13 from '../images/twoItem1-3.png'

import Aos from 'aos'
import 'aos/dist/aos.css'
import { useEffect } from 'react'

export default function Two() {
  useEffect(() => {
    Aos.init({ duration: 500 })
  }, [])
  const twoItem = [
    {
      title: ' Parallax Scrolling',
      logo: twoItem1,
      img: twoItem11,
      h1: 'The F2E ',
      text: 'redesign',
    },
    {
      title: ' Canvas',
      logo: twoItem2,
      img: twoItem12,
      h1: '',
      text: 'PDFWeb!',
    },
    {
      title: 'Scrum JS draggable',
      logo: twoItem3,
      img: twoItem13,
      h1: 'Scrum ',
      text: ' Scrum',
    },
  ]
  return (
    <section className="two">
      <img className="twoLogo" src={twoLogo} alt="" />
      <div className="twoText1"></div>
      <div className="underLine"></div>
      {twoItem.map((item, index) => {
        return <TwoItem key={index} item={item} />
      })}
    </section>
  )
}

function TwoItem({ item }) {
  const { title, logo, img, h1, text } = item

  return (
    <section
      data-aos="fade-left"
      data-aos-offset="300"
      data-aos-easing="ease-in-sine"
      className="twoItem"
    >
      <div className="itemNav">
        <div>{title}</div>
        <img src={logo} alt="" />
      </div>
      <div className="itemContainer">
        <div className="twoItemImgContainer">
          <img src={img} alt="" />
        </div>
        <div className="itemText">
          <h1>{h1}</h1>
          <p>{text}</p>
          <div className="buttonContainer">
            <button></button>
            <button></button>
          </div>
        </div>
      </div>
    </section>
  )
}
